import React from 'react'

function Contacto() {
  return (
    <section  id='contacto' className='conactt'>
      <div class="content">



<div data-aos="flip-left" class="contact-wrapper animated bounceInUp">
    <div class="contact-form">
        
        <h2 className='text-center py-3'>Contacto</h2>
        <p className='formm'>Estamos interesados en saber su opinión sobre nosotros, ya sea algo positivo o alguna crítica constructiva, si tiene alguna pregunta no dude en ponerse en contacto con nosotros ya sea por este medio o por <a href='https://www.instagram.com/3directimpresiones/' >instagram</a>.</p>
        <form className='formula'
      
      
      action="https://formsubmit.co/impresiones.3direct@gmail.com"  method="POST"
      target="_blank"
    ><div className='nomyape'>
      <div className="nom1 mb-3 pt-0">
      <h6 className='cas1'>Nombre</h6>
        <input
          type="text"
          
          name="nombre"
          className="cass2 px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-0 shadow outline-none focus:outline-none focus:ring w-full"
          required
        />
      </div>
      <div className="ape1 mb-3 pt-0">
      <h6 className='cas2'>Apellido</h6>
        <input
          type="text"
         
          name="apellido"
          className="cass2 px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-0 shadow outline-none focus:outline-none focus:ring w-full"
          required
        />
      </div>
      </div>
      <div className="mb-3 pt-0">
      <h6 className='cas3'>Email</h6>
        <input
          type="email"
          
          name="email"
          className="cass3 px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-0 shadow outline-none focus:outline-none focus:ring w-full"
          required
        />
      </div>
      <div className="mb-3 pt-0">
        <h6 className='cas4'>Mensaje</h6>
        <textarea
          
          name="mensaje"
          className="cass3 px-3 py-3 placeholder-gray-400 text-gray-600 relative bg-white bg-white rounded text-sm border-0 shadow outline-none focus:outline-none focus:ring w-full"
          required
        />
      </div>
      <div className="mb-3 pt-0">
        <button
          className="bttt bg-blue-500 text-white active:bg-blue-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
          type="submit"
        ><h6 className='boton'>
          Enviar Mensaje</h6>
        </button>
      </div>
    </form>
       
   
    </div>
    
        
   
    </div>
   
</div>



    </section>
    
  )
}

export default Contacto