import React from 'react';
import {NavLink} from 'react-router-dom';
import logo from "../Archivos/Imagenes/3dpng.png"

import Navbar from 'react-bootstrap/Navbar';



function NavBar(){ 
    return(
        <div className='navvvvv'>
            <nav className="navbar navbar-expand-lg">
  <div className="container-fluid">
  <Navbar.Toggle aria-controls={`offcanvasNavbar-expand`}></Navbar.Toggle>
    <a className="logonb navbar-brand" href='#inicio'><img src={logo} alt="logo img"></img></a>
    <button className="bottnb navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
    <i class="fa-solid fa-ellipsis-vertical"></i>
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse ps-5" id="navbarNavDropdown">
      <ul className="navbar-nav">
        <li className="nav-item">
          <a className="nav-link" href="#inicio">Inicio</a>
        </li>
        <li className="nav-item">
          <a className="nav-link " href="#nosotros">Nosotros</a>
        </li>
        <li className="nav-item">
          <a className="nav-link " href="#contacto">Contacto</a>
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" to="#" id="navbarDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Productos
          </a>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
            <li><a className="dropdown-item" href="https://3direct.mercadoshops.com.ar/">Tienda Online</a></li>
            <li><a className="dropdown-item" href="#cotiza">Cotiza tu idea</a></li>
            <li><a className="dropdown-item" href="#">Lista de precio Mayorista</a></li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</nav>
        </div>
    )
}

export default NavBar