import {Container, Row, Col} from "react-bootstrap"
import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/pagination";
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";

// import required modules
import { EffectCreative } from "swiper";
import foto1 from "../Archivos/Imagenes/otto.jpeg"
import foto2 from "../Archivos/Imagenes/borabora.jpeg"
import foto3 from "../Archivos/Imagenes/bulbasaur.JPG"
import foto4 from "../Archivos/Imagenes/charizard.jpeg"
import foto5 from "../Archivos/Imagenes/charmander.JPG"
import foto6 from "../Archivos/Imagenes/copafifa.JPG"
import foto7 from "../Archivos/Imagenes/florksmuchos.jpg"
import foto8 from "../Archivos/Imagenes/llaveros.webp"
import foto9 from "../Archivos/Imagenes/macetagroot.jpg"
import foto10 from "../Archivos/Imagenes/trofeoscumbiaton.JPG"
import foto11 from "../Archivos/Imagenes/macetapokebola.JPG"
import foto12 from "../Archivos/Imagenes/Thor.jpg"




function Inicio() {
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);
  return (
    <section id="inicio" data-aos="fade-right" className="inicio">
     <Container >
        <h2 className='text-center p-3'>Nuestros productos</h2>
       
       <Row>
        <Col>
      <Swiper
        grabCursor={true}
        effect={"creative"}
        creativeEffect={{
          prev: {
            shadow: true,
            translate: [0, 0, -400],
          },
          next: {
            translate: ["100%", 0, 0],
          },
        }}
        modules={[EffectCreative]}
        className="mySwiper"
      >
   <SwiperSlide><img src={foto1} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto2} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto3} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto4} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto5} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto6} alt="foto1"></img></SwiperSlide>
      </Swiper>
      </Col>
      <Col>
      <Swiper
        grabCursor={true}
        effect={"creative"}
        creativeEffect={{
          prev: {
            shadow: true,
            translate: ["-120%", 0, -500],
          },
          next: {
            shadow: true,
            translate: ["120%", 0, -500],
          },
        }}
        modules={[EffectCreative]}
        className="mySwiper2"
      >
       <SwiperSlide><img src={foto7} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto8} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto9} alt="foto1"></img></SwiperSlide>
      </Swiper>
      </Col>
      <Col>
      <Swiper
        grabCursor={true}
        effect={"creative"}
        creativeEffect={{
          prev: {
            shadow: true,
            translate: ["-20%", 0, -1],
          },
          next: {
            translate: ["100%", 0, 0],
          },
        }}
        modules={[EffectCreative]}
        className="mySwiper3"
      >
       <SwiperSlide><img src={foto10} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto11} alt="foto1"></img></SwiperSlide>
        <SwiperSlide><img src={foto12} alt="foto1"></img></SwiperSlide>
      </Swiper>
      </Col>
      <p className='text-center p-3'>Desliza hacia la derecha para ver mas fotos</p>
      </Row>
      </Container>
      </section>
  );
}

export default Inicio;
