import React from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import foto1 from "../Archivos/Imagenes/curascr.jpg"
import foto2 from "../Archivos/Imagenes/curascr2.jpg"
import foto3 from "../Archivos/Imagenes/curascr3.jpg"
import foto4 from "../Archivos/Imagenes/curascr4.jpg"
import foto5 from "../Archivos/Imagenes/comoimprimev2.jpg"
import foto6 from "../Archivos/Imagenes/capaxcapa.gif"
import foto7 from "../Archivos/Imagenes/timelapse2.gif"
function Cotiza() {
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);
  return (
    <div data-aos="fade" id="cotiza" className='cotizz'>
      <div className='container'>
        <div>
          <br></br>
          <h4>
          ¿Cuál es el proceso de impresión una vez que tenemos el archivo?

          </h4>
          <p className=''>
          Una vez que tenemos el diseño 3d en archivo .stl o .obj(formatos
            estándar para las tecnologías de fabricación aditiva) vamos a abrir
            el archivo con un programa laminador, como por ejemplo el cura de
            ultimaker, configuramos los parametros de impresion(temperaturas,
            velocidades, cantidad de relleno) y exportamos el archivo con la
            extensión .gcode a una tarjeta sd, una vez exportado, colocamos la
            tarjeta en nuestra impresora y empezamos la impresión.

          </p>
          <br></br>
          <img src={foto1} className='w-25'></img>
          <img src={foto2} className='w-25'></img>
          <img src={foto3} className='w-25'></img>
          <img src={foto4} className='w-25'></img>

          <br></br>
          <br></br>
          <h5>¿Dónde puedo buscar modelos 3D?
 </h5>
          <div className='container'>
            <div className='row'>
              <h7>
              Hay varias páginas web para buscar modelos, ya sean pagos o gratuitos.

              </h7>
              <div className='col'>
                <p className=''>
                  <h6 class='text-decoration-underline'>Bancos de stl:</h6>
                  <ul>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://www.stlfinder.com/'
                      >
                        StlFinder
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://www.yeggi.com/'
                      >
                        Yeggi
                      </a>
                    </li>
                    <br></br>

                    <img src={foto5} className='w-50'></img>
                    <img src={foto6} className='w-50'></img>
                  </ul>
                </p>
              </div>
              <div className='col'>
                <h5>Cotiza tu impresion!</h5>
                <form action="https://formsubmit.co/impresiones.3direct@gmail.com"  method="POST">
                  <div class='input-group'>
                    <input
                      type='text'
                      placeholder='Nombre'
                      aria-label='First name'
                      class='cott form-control'
                    />
                    <input
                      type='text'
                      placeholder='Apellido'
                      aria-label='Last name'
                      class='cott form-control'
                    />
                  </div>
                  <br></br>
                  <div class='mb-3'>
                    <input
                      type='email'
                      placeholder='Correo Electronico'
                      class='cott form-control'
                      id='exampleInputEmail1'
                      aria-describedby='emailHelp'
                    />
                    <div id='emailHelp' classname='form-text'>
                      Nunca compartiremos su correo electrónico con nadie.
                    </div>
                  </div>
                  <h6>
                  En la descripción podes aclarar color, tamaño, algún detalle que quieras que modifiquemos o alguna duda que tengas.{" "}
                  </h6>
                  <div class='form-floating'>
                    <textarea
                      class='cott form-control'
                      placeholder='Leave a comment here'
                      id='floatingTextarea2'
                    ></textarea>
                    <label for='floatingTextarea2'>Descripcion</label>
                  </div>
                  <br></br>
                  <div class='input-group mb-3 '>
                    <input
                      type='file'
                      class='cott form-control'
                      id='inputGroupFile02'
                    />
                    <label
                      class='input-group-text btn-primary'
                      for='inputGroupFile02'
                    >
                      Subir Archivo
                    </label>
                  </div>
                  <button type='submit' class='btn btn-primary'>
                    Enviar
                  </button>
                </form>
              </div>
            </div>
            <div className='row'>
              <div className='col'>
                <p className=' '>
                  <h6 classname='text-decoration-underline'>Gratuitos y pagos:</h6>
                  <ul>
                    <h7>Gratuitos</h7>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://www.thingiverse.com/'
                      >
                        Thingiverse
                      </a>
                    </li>
                    <h7>
                      Pagos, que tambien tienen algunos archivos gratuitos
                    </h7>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://cults3d.com/es'
                      >
                        Cults
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://free3d.com/es/'
                      >
                        Free3D
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://grabcad.com/library'
                      >
                        Grabcad Library
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://fab365.net/'
                      >
                        Fab365
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://pinshape.com/'
                      >
                        Pinshape
                      </a>
                    </li>
                    <li>
                      <a
                        className='stlss text-decoration-none'
                        href='https://www.yeggi.com/'
                      >
                        Yeggi
                      </a>
                    </li>
                  </ul>
                </p>
              </div>
              <div className='col'>
                <br></br>
                <img src={foto7} className='w-50'></img>
                <br></br>
                <br></br>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Cotiza;
